.stars {
  display: flex;
  
  &__star {
    height: $size-star;
    width: $size-star;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
  }
}

.stars--4 > .stars__star:nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
}
